import { BrowserRouter as Router, Route, Routes, useLocation } from 'react-router-dom'
import store from './store'
import { Helmet, HelmetProvider } from 'react-helmet-async';
import { Provider } from 'react-redux';


import AnimatedRoutes from 'Routes';



function App() {
  const location = useLocation()
  return (
    <HelmetProvider>
      <Helmet>
        <title>Centro Reino da Mata | I.I.F.A</title>
        <meta name="description" content="Instituto de Investigacin y Formacin Afro-umbandista." />
        <meta name="keywords" content='instituto, educacin on-line, cultura, religin, umbanda, quimbanda, cursos.' />
        <meta name="robots" content='all' />
        <link rel="canonical" href="https://www.centroumbandistareinodamata.com/" />
        <meta name="author" content='Reino da Mata' />
        <meta name="publisher" content='Reino da Mata' />
      </Helmet>
      <Provider store={store}>
      <Router>
        <Routes>
          <AnimatedRoutes/>
        </Routes>
      </Router>
      </Provider>
    </HelmetProvider>
    
    
  );
}

export default App;
